a {
  color: #000;
  text-decoration: none;
}

body {
  box-sizing: border-box;
  margin: 0 10px 10px;
}

.search-form {
  width: 100%;
  height: 60px;
  background-color: #82cbd6;
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  box-shadow: 0 2px 5px -1px rgba(50, 50, 93, .25), 0 1px 3px -1px rgba(0, 0, 0, .3);
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 320px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding-top: 70px;
  display: grid;
}

.photo-card {
  box-shadow: 0 2px 5px -1px rgba(50, 50, 93, .25), 0 1px 3px -1px rgba(0, 0, 0, .3);
}

.photo-card img {
  height: calc(100% - 70px);
  width: 100%;
  object-fit: cover;
}

.info {
  justify-content: space-around;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.input {
  width: 264px;
  height: 35px;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 4px;
  box-shadow: inset 3px 3px 6px #ccdbe8, inset -3px -3px 6px 1px rgba(255, 255, 255, .5);
}

.search-btn {
  cursor: pointer;
  height: 35px;
  background-color: #eee;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 4px;
  box-shadow: 0 1px 2px rgba(60, 64, 67, .3), 0 1px 3px 1px rgba(60, 64, 67, .15);
}

